.wrapper{
    height: 100vh;
    max-width: 100%;
    background-color: #f3f3f3;
    font-family: 'Oswald', sans-serif;
}

.centerText{
    position: absolute; 
    top: 30%;
    left: 0; 
    right: 0; 
    margin-left: auto; 
    margin-right: auto; 
    width: 500px;
}

.topText{
    font-size: 52px;
    text-align: center;
    color: #173e43;
}

.horizontalLine{
    width: 470px;
    margin-left: auto; 
    margin-right: auto; 
    color: #cacaca;
    font-weight: 500;
    
}

.bottomText{
    position: relative;
    top: -30px;
    font-size: 130px;
    text-align: center;
    color:#173e43;
}
.glow {
    color: #3FB0AC;
    font-size: 40px;
    line-height: 9vw;
    text-align: center;
  }
  .glow {
    animation: glow 5.2s ease infinite;
    -moz-animation: glow 5.2s ease infinite;
    -webkit-animation: glow 5.2s ease infinite;
  }
  @keyframes glow {
    0%,
    100% {
      text-shadow: 0 0 1vw #3FB0AC, 0 0 3vw  #3FB0AC, 0 0 10vw  #3FB0AC, 0 0 10vw#3FB0AC, 0 0 .4vw#3FB0AC, .5vw .5vw .1vw #3FB0AC;
      color: #3FB0AC;
    }
    50% {
      text-shadow: 0 0 .5vw #3FB0AC, 0 0 1.5vw #3FB0AC, 0 0 5vw #3FB0AC, 0 0 5vw  #3FB0AC, 0 0 .2vw  #3FB0AC, .5vw .5vw .1vw #3FB0AC;
      color:  #f3F3F3;
    }
  }